<template>
    <div class="chat-emojis-dropdown" :class="{ 'dropdown-open': dropdownOpen }">
      <button @click="chatToggleDropdown" class="button-toggle">
        <IconEmojisGradient />
        <span class="gradient-green">EMOJIS</span>
      </button>
      <div class="dropdown-window">
        <div class="window-inner">
          <div class="inner-list">
            <button
              v-for="(item, index) in allMedia"
              :key="index"
              @click="chatSelect(item)"
              class="media-button"
            >
              <!-- Wenn es ein Emoji (Text) ist -->
              <template v-if="item.type === 'emoji'">
                {{ item.content }}
              </template>
              <!-- Wenn es ein Bild (GIF, PNG oder WebP) ist -->
              <template v-else>
                <img :src="item.content" :alt="item.type" />
              </template>
            </button>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import IconEmojisGradient from '@/components/icons/IconEmojisGradient';
  
  export default {
    name: 'ChatEmojisDropdown',
    components: {
      IconEmojisGradient
    },
    data() {
      return {
        dropdownOpen: false,
        // Basistext-Emojis und einige zustzliche Kategorien
        dropdownEmojis: [
          // Smileys & Emotionen
          '', '', '', '', '', '', '', '', '', '',
          '', '', '', '', '', '', '', '', '', '',
          '', '', '', '', '', '', '', '', '', '',
          '', '', '', '', '', '', '', '', '', '',
          '', '', '', '', '', '', '', '', '', '',
          '', '', '', '', '', '', '', '', '', '',
          '', '', '', '', '', '', '', '', '', '',
          '', '', '', '', '', '', '', '', '', '',
          '', '', '', '', '', '', '', '', '', '',
          '',
    
          // Flaggen (Auswahl der Basic-Flaggen)
          '', '', '', '', '',
    
          // Tiere (Beispiel: Nur ein Emoji, erweitere nach Bedarf)
          '',
    
          // Gesten & Hnde
          '', '', '', '', '', '', '', '', '', '',
          '', '', '', '', '', '',
    
          // Herzen & Liebe
          '', '',
    
          // Zustzliche Kategorien
          // Natur & Wetter
          '',
    
          // Essen & Trinken
          '', '', '', ''
        ],
    
        // Hier binden wir die lokalen GIFs ein
        dropdownGifs: [
          require('@/assets/img/emojis/kekwlaugh.gif'),
          require('@/assets/img/emojis/lil_swag.gif'),
          require('@/assets/img/emojis/malariadancep2.gif'),
          require('@/assets/img/emojis/pain.gif'),
          require('@/assets/img/emojis/pedroraccoon.gif'),
          require('@/assets/img/emojis/reverso.gif'),
          require('@/assets/img/emojis/rickRoll.gif'),
          require('@/assets/img/emojis/rollAngry.gif'),
          require('@/assets/img/emojis/rollCry.gif'),
          require('@/assets/img/emojis/rollDJ.gif'),
          require('@/assets/img/emojis/rollDrool.gif'),
          require('@/assets/img/emojis/rollKing.gif'),
          require('@/assets/img/emojis/rollSad.gif'),
          require('@/assets/img/emojis/rollSigh.gif'),
          require('@/assets/img/emojis/rollSkull.gif'),
          require('@/assets/img/emojis/tada_animated.gif'),
          require('@/assets/img/emojis/theRockBrows.gif'),
          require('@/assets/img/emojis/travisApology.gif'),
          require('@/assets/img/emojis/twitchlive.gif'),
          require('@/assets/img/emojis/batmanhmm.gif'),
          require('@/assets/img/emojis/catjam.gif'),
          require('@/assets/img/emojis/cry_2.gif'),
          require('@/assets/img/emojis/dance.gif')
        ],
    
        // Hier binden wir die lokalen PNG/WebP-Dateien ein
        dropdownPngs: [
          require('@/assets/img/emojis/DiCaprioLaugh.png'),
          require('@/assets/img/emojis/heyGuys.png'),
          require('@/assets/img/emojis/Kappa.png'),
          require('@/assets/img/emojis/komodoHype.png'),
          require('@/assets/img/emojis/kreyGasm.png'),
          require('@/assets/img/emojis/LUL.png'),
          require('@/assets/img/emojis/mischief.png'),
          require('@/assets/img/emojis/notLikeThis.png'),
          require('@/assets/img/emojis/pepeCopium.png'),
          require('@/assets/img/emojis/ResidentSleeper.png'),
          require('@/assets/img/emojis/seemsGood.png'),
          require('@/assets/img/emojis/shrug.png'),
          require('@/assets/img/emojis/stare.png'),
          require('@/assets/img/emojis/sussy.png'),
          require('@/assets/img/emojis/theIlluminati.png'),
          require('@/assets/img/emojis/widePeepoHappy.png'),
          require('@/assets/img/emojis/wutFace.png'),
          require('@/assets/img/emojis/baller.png'),
          require('@/assets/img/emojis/bibleThump.png'),
          require('@/assets/img/emojis/bombastic.png'),
          // WebP-Beispiele
          require('@/assets/img/emojis/whatinthegoddamn.webp'),
          require('@/assets/img/emojis/baldwinhand.webp')
        ]
      };
    },
    computed: {
      allMedia() {
        // Konvertiere die textbasierten Emojis in Objekte
        const emojis = this.dropdownEmojis.map(content => ({
          type: 'emoji',
          content
        }));
        // Konvertiere die GIFs in Objekte
        const gifs = this.dropdownGifs.map(content => ({
          type: 'gif',
          content
        }));
        // Konvertiere die PNGs (bzw. WebP) in Objekte
        const pngs = this.dropdownPngs.map(content => ({
          type: 'png',
          content
        }));
    
        const all = [...emojis, ...gifs, ...pngs];
        return all;
      }
    },
    methods: {
      chatToggleDropdown() {
        this.dropdownOpen = !this.dropdownOpen;
      },
      chatSelect(item) {
        console.log('Item selected:', item);
        this.chatToggleDropdown();
        if (item.type === 'emoji') {
          // Diese Methoden mssen im Eltern-Component existieren
          if (this.$parent && this.$parent.chatAddEmoji) {
            this.$parent.chatAddEmoji(item.content);
          } else {
            console.warn('chatAddEmoji im Eltern-Component nicht gefunden.');
          }
        } else {
          if (this.$parent && this.$parent.chatAddMedia) {
            this.$parent.chatAddMedia({ url: item.content, type: item.type });
          } else {
            console.warn('chatAddMedia im Eltern-Component nicht gefunden.');
          }
        }
      }
    },
    created() {
      // Dropdown schlieen, wenn auerhalb geklickt wird
      document.addEventListener('click', event => {
        if (!this.$el.contains(event.target) && this.dropdownOpen) {
          this.chatToggleDropdown();
        }
      });
    }
  };
  </script>
  
